	function subTwoDecimal(digital) {
	  if (digital != null) {
	    var num = digital.toString();
	    if ((num).indexOf('.') != -1) {
	      // var num = digital;
	      //
	      var num2 = num.split('.');
	      var num4;
	      if (num2.length == 2) {
	        var num3 = num2[1].split('');
	        if (num3.length >= 2) {
	          num4 = num2[0] + '.' + num3[0] + num3[1];
	        }
	        else {
	          num4 = num2[0] + '.' + num2[1] + '0';
	        }
	      }
	      else {
	        num4 = num2[0];
	      }
	      return num4;
	    }
	    else {
	      return num + '.00';
	    }
	  }
	  else {
	    // alert('NaNundefined,');
	  }
	}

	//
	function subTwoDecimal2(digital) {
	  if (digital != null) {
	  	digital = digital.replace('%', '');
	  }
	  var sub = subTwoDecimal(digital);
	  return sub;
	}

	// 
    function deepClone(obj){
        var str, newobj = obj.constructor === Array ? [] : {};
        if(typeof obj !== 'object'){
            return;
        } else if(window.JSON){
            str = JSON.stringify(obj), //
            newobj = JSON.parse(str); //
        } else {
            for(var i in obj){
                newobj[i] = typeof obj[i] === 'object' ? 
                cloneObj(obj[i]) : obj[i]; 
            }
        }
        return newobj;
    }

